import React,{useEffect,useState} from 'react';
import clsx from "clsx"
import { IconButton } from "@material-ui/core";
import { makeStyles } from '@material-ui/core/styles';
import ImportantDevicesIcon from '@material-ui/icons/ImportantDevices';
import MonetizationOnIcon from '@material-ui/icons/MonetizationOn';
import StarBorderIcon from '@material-ui/icons/StarBorder';
import AssignmentIcon from '@material-ui/icons/Assignment';
import ScrollTo from "../common/ScrollTo";
import { debounce, classList } from "../utils";
const useStyles = makeStyles({
    root: {
    },
    topBar: {
        height: 3,
        border: "2px solid rgba(var(--primary),0.25)",

        "&, & .icon-holder": {
          transition: "all 250ms ease-in-out",
        },
    
        "& .icon-holder": {
          background: "rgba(var(--primary),0.25) !important",
        },
    
        "&:hover": {
          border: "2px solid rgba(var(--primary),1)",
          "& .icon-holder": {
            background: "rgba(var(--primary),1) !important",
          },
        },
    
    },
});
export default function SimpleBottomNavigation() {
    const classes = useStyles();
    const [isTop, setIsTop] = useState(true);
    const [isClosed, setIsClosed] = useState(true);
  
    let scrollableElement = document.querySelector(".scrollable-content");
    if (!scrollableElement) scrollableElement = window;
  
    let handleScrollRef = null;
    let toggleIcon = isClosed ? "menu" : "close";
  
    const handleScroll = () => {
      return debounce(({ target: { scrollTop } }) => {
        let isCurrentTop = scrollTop < 100 || scrollableElement.scrollY < 100;
        setIsTop(isCurrentTop);
      }, 20);
    };
  
    handleScrollRef = handleScroll();
  
    const close = () => {
      setIsClosed(false);
    };
  
    useEffect(() => {
      if (scrollableElement) {
        scrollableElement.addEventListener("scroll", handleScrollRef);
      }
  
      return () => {
        if (scrollableElement) {
          scrollableElement.removeEventListener("scroll", handleScrollRef);
        }
      };
    }, [scrollableElement, handleScrollRef]);
    return (
      <section
      className={classList({
       
        closed: isClosed,
      })}>
           <ScrollTo to="portfoli2" onScroll={close}>
            <IconButton 
             className={clsx(
                "p-12px rounded flex items-center text-white",
                classes.topBar
              )}
            >
                <ImportantDevicesIcon fontSize="small" color="#ffffff" />
                <p className="mt-0 mb-0 text-11 font-semibold text-white">  Productos Recomendados</p>
            </IconButton>
            </ScrollTo>
            <ScrollTo to="testimonial2" onScroll={close}>
            <IconButton  
              className={clsx(
                "p-12px rounded flex items-center text-white",
                classes.topBar
              )}>
                <StarBorderIcon fontSize="small" color="#ffffff" />
                <p className="mt-0 mb-0 text-11 font-semibold text-white">Experiencia</p>
                {/* <p className="bg-primary rounded-ll text-15 text-white">Mas Vendido</p> */}
            </IconButton>
            </ScrollTo>
        </section>
    );
}
